import styled, { css } from 'styled-components';


export const Button = styled.button`
    background: none;
    padding: 0.375em 0.75em;
    ${(props) => !props.onDark && css`
        color: inherit;
    `}
    ${(props) => props.onDark && css`
        color: white;
    `}
    display: inline-block;
    margin: 0.4em 0.25em;
    border-radius: 0.25em;
    vertical-align: middle;
    text-align: center;
    font-size: inherit;

    ${(props) => !props.primary && !props.subtle && css`
        border: 0.0625em solid ${(props) => props.theme.colors.subtle};
        &:hover {
            border: 0.0625em solid ${(props) => props.theme.colors.accent};
            color: ${(props) => props.theme.colors.accent};
        }
    `}

    ${(props) => props.primary && css`
        border: 0.0625em solid ${(props) => props.theme.colors.accent};
        background: ${(props) => props.theme.colors.accent};
        color: ${(props) => props.theme.colors.darkText};
    `}


    ${(props) => props.subtle && css`
        border: 0px none;
        &:hover {
            background-color: ${(props) => props.theme.colors.subtleHighlight};
        }
    `}
    

    transition: color 0.5s, border 0.5s, filter 0.5s, background-color 0.25s;
    cursor: pointer;
`