import React, { useState, useEffect } from 'react';
import { GlobalStyle } from './theme/GlobalStyle';
import styled, { css, ThemeProvider } from 'styled-components';
import { snow, polar } from './theme/themes';
import { useThemeMode } from './theme/useThemeMode';
import { Button } from './component/buttons'
import socketIOClient from "socket.io-client";
import './App.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { faCircle } from '@fortawesome/free-regular-svg-icons'
const ENDPOINT = "http://nc300cyberdemo.azurewebsites.net";

function App() {
  const [time, setTime] = useState(0)
  const [theme, toggleTheme] = useThemeMode();
  const themeMode = theme === 'snow' ? snow : polar;
  const [text, setText] = useState("")
  const [value, setValue] = useState(5)

  return (
    <ThemeProvider theme={themeMode}>
      <GlobalStyle/>
      <main className="Main">
        <div style={{display: "block", fontSize: "3em"}}>{time}</div>
        <Board timeChange={(x)=>{setTime(x)}}/>
      </main>
    </ThemeProvider>
  );
}

const BoardMain = styled.div`
  max-width: 80vw;
  width: 500px;
  aspect-ratio: 1;
  //background-color: ${(props)=>props.theme.colors.elevated};
  border-radius: 1em;
  border: 3px solid ${(props) => props.theme.colors.subtle};
  display: flex;
  flex-wrap: wrap;
  overflow:hidden;
`

const StyledTile = styled.button.attrs(props => ({
  style: {
    background: `rgba(0, 255, 149, ${(props.votes / props.totalVotes) || 0})`,
  },
  disabled: props.disabled
}))`
transition: background 0.3s;
width: 33.333333%;
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1;
  border: 0.5px solid ${(props) => props.theme.colors.elevated};
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  cursor: pointer;
  color: inherit;
  position: relative;

  &:hover {
    &::before {
      content: "";
      position: absolute;
      left: 0; right: 0; bottom: 0; top: 0;
      width: 100%;
      height: 100%;
      background: rgba(255,255,255,0.05)
    }  
  }
  &:after {
    content: "";
    background: ${(props) => props.theme.colors.accent};
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    transition: all 0.6s
  }
  
  ${(props) => !props.disabled && css`
    &:active:after {

      left: 50%;
      right: 50%;
      top: 50%;
      bottom: 50%;
      opacity: 0.5;
      transition: 0s
    }
  `}
`

function Tile(props){
  const [time, setTime] = React.useState(0);
  
  if (time > 0) {
    setTimeout(() => setTime(time - 1), 1000);
  }

  if(window.location.hash == "#admin" && props.personTurn){
    return (
      <>
        <StyledTile disabled={props.result != 0} onClick={()=>{
          if(time <= 0){
            props.click()
            setTime(1)
          }
        }} votes={props.votes} totalVotes={props.totalVotes}>
          {props.result == 1 && 
            <p style={{fontSize: "7em", color: "red", lineHeight: "0"}}><FontAwesomeIcon icon={faTimes} /></p>
          }
          {props.result == 2 && 
            <p style={{fontSize: "7em", color: "#00ff95", lineHeight: "0"}}><FontAwesomeIcon icon={faCircle} /></p>
          }
        </StyledTile>
      </>
    )
  } else {
    return (
      <>
        <StyledTile disabled={time > 0 || !props.enabled || props.result != 0} onClick={()=>{
          if(time <= 0){
            props.click()
            setTime(1)
          }
        }} votes={props.votes} totalVotes={props.totalVotes}>
          {props.result == 1 && 
            <p style={{fontSize: "7em", color: "red", lineHeight: "0"}}><FontAwesomeIcon icon={faTimes} /></p>
          }
          {props.result == 2 && 
            <p style={{fontSize: "7em", color: "#00ff95", lineHeight: "0"}}><FontAwesomeIcon icon={faCircle} /></p>
          }
          {props.result == 0 && <p style={{opacity: (props.votes / props.totalVotes * 5) || 0, fontSize: Math.min(Math.max(1, props.votes / props.totalVotes * 10), 4) + "em"}}>{props.votes > 0 && props.votes}</p>}
        </StyledTile>
      </>
    )
  }
}

function Board(props) {
  const [sock, setSock] = useState(null)
  const [personTurn, setPersonTurn] = useState(null)
  const [boardState, setBoardState] = useState(
    [
      0, 0, 0,
      0, 0, 0,
      0, 0, 0
    ]
  )
  const [results, setResults] = useState(
    [
      0, 0, 0,
      0, 0, 0,
      0, 0, 0
    ]
  )
  useEffect(() => {
    const socket = socketIOClient(ENDPOINT, { transports : ['websocket'] });
    setSock(socket)
    socket.on("message", data => {
      const res = JSON.parse(data)
      setBoardState(res.board)
      setResults(res.results)
      setPersonTurn(res.personTurn)
      props.timeChange(res.time)
    });
  }, []);

  var totalVotes = boardState.reduce((a, b) => a + b, 0)

  return (
    <>
      <BoardMain>
        {
          [
            ...Array(9),
          ].map((value, index) => (
            <Tile 
              key={index}
              idx={index} 
              votes={boardState[index]} 
              totalVotes={totalVotes}
              enabled={!personTurn}
              result={results[index]}
              personTurn={personTurn}
              click={()=>{
                if(!personTurn) {
                  var newBoard = boardState.slice()
                  newBoard[index] ++;
                  sock.send(JSON.stringify({vote: index}))
                  setBoardState(newBoard)
                } else if(window.location.hash == "#admin") {
                  console.log("admin vote")
                  sock.send(JSON.stringify({adminVote: index}))
                }
              }}
            />
          ))
        }
      </BoardMain>
    </>
  )
}


export default App;
